<template>
<div class = "form">
  <h3>Name : </h3>
  <input v-model= "name"></input><br>
  <h3>Phone No. : </h3>
  <input v-model= "phone" type="number"></input><br>
  <h3>Email : </h3>
  <input v-model= "email" type="email"></input><br>
  <h3>Complaint : </h3>
  <textarea rows="4" cols="30" v-model= "comp" type="text"></textarea><br><br>
  <h3>Upload image : </h3>
  <input type="file" class="file-select" accept="image/*" @change="onFileSelected"/><br><br>

  <button class="file-submit" @click = "onUpload">SUBMIT</button><br><br>
  
  <button @click = "add(name,phone, email, comp)">Click</button><br><br>

</div>

</template>

<script>

import firebase from 'firebase/app'
import {db, auth, Timestamp, storageRef, storageService } from "../firebase"

export default {
  name: 'HelloWorld',
  data () {
    return {
      name: '',
      phone: '',
      email: '',
      comp: '',
      imgurl: '',
      selectedFile: []      
    }
  },
  firestore : {
  documents: db.collection('name'),
  },
  methods : {
    add (name, phone, email, comp) {

    const uri = this.imgurl
    const date = new Date();
    db.collection('aa').add({
    name, phone, email, comp, date, uri });
    this.name = '';
    this.phone = '';
    this.email = '';
    this.comp = '';
    this.imgurl = '';
    this.uri ='';
    },

    onFileSelected(event) {
      this.selectedFile = event.target.files[0 ]
    },

    onUpload(event) {
      const uploadTask = storageRef.child(`images/${this.selectedFile.name}`).put(this.selectedFile); //create a child directory called images, and place the file inside this directory

  uploadTask.on('state_changed', (snapshot) => {
  // Observe state change events such as progress, pause, and resume
  }, (error) => {
    // Handle unsuccessful uploads
    console.log(error);
  }, () => {
     // Do something once upload is complete
    storageRef.child(`images/${this.selectedFile.name}`).getDownloadURL().then((url) => {
    this.imgurl = url;
    
    });
     console.log(this.imgurl);

  });
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
h3 {
  margin-left : 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



